
//  import html2canvas from  '../../../../static/lib/js/html2canvas-beta5';

  var $sectionList = function(id){
    var _self = this ;
    this.H.toAjax(true).then(function(comData){ //data 公用参数
      comData.data = {activityId:id,type:"attendance"}; //type 会改变 课程
      _self.A.post("/v1/section/list",comData).then(function(result){

        console.log("素材",result);
        if(result.data.state.code == 0){
          var sections = result.data.data.sectionResult.sections;
          sections.forEach(function(obj){ obj.isShow = false; })
          sections.forEach(function(obj){ obj.showEditInput = false; })
          console.log(sections);
          _self.sections = sections;
          _self.sectionPages = result.data.data.sectionResult.page ;
        }

      })
    })

  }
  var $create  =  function(obj){
    var _self = this ;
    this.H.toAjax(true).then(function(comData){ //data 公用参数

      comData.data = obj;
      _self.A.post("/v1/section/create",comData).then(function(result){
        _self.section.isShow = false;
        _self.section.link = "";
        _self.section.lessonName = "";
        console.log(result);
        if(result.data.state.code == 0) _self.$emit("modalAlert",result.data.state.msg,"确定");
        var _id = _self.section.activityId ;
        $sectionList.call(_self,_id);

      })
    })

  }

  var del = function (obj) {
    var _self = this;
    this.H.toAjax(true).then(function (comData) { //data 公用参数

      comData.data = obj;
      _self.A.post("/v1/section/delete", comData).then(function (result) {
        console.log(result);
        console.log(obj);



        if (result.data.state.code == 0) _self.$emit("modalAlert", result.data.state.msg, "确定");
      })
    })

  }

  var edit = function (obj) {
    var _self = this;
    this.H.toAjax(true).then(function (comData) { //data 公用参数

      comData.data = obj;
      _self.A.post("/v1/section/create", comData).then(function (result) {
        console.log(result);
        console.log(obj);
        if (result.data.state.code == 0){
          _self.$emit("modalAlert", "修改成功");
        }
        else{
          _self.$emit("modalAlert", result.data.state.msg);
        }
      })
    })

  }

  var getShareCardDate = function(timeStamp){
    var time = new Date(timeStamp);
    var fullYear = time.getFullYear();
    var month = time.getMonth();
    var date = time.getDate();
    var day = time.getDay();
    var day_cn = ['周日','周一','周二','周三','周四','周五','周六'];
    //return fullYear + '-' + （month+1） + '-' + date + ' ' + day_cn[day];
    return fullYear + '年' + (month+1) + '月' + date + '日'
  }


