      goMoment(){
        this.$router.push({path:"/admin/punchShow/meet",query:{id:this.activityId,type:this.type}});
      }
      ,$toAddSection:function(){
        this.section.isShow = true ;
        this.section.type = "add";
      }
      ,$doAddSection:function(){

        if(this.$refs.sectionName.valid && this.$refs.link.valid ){
          this.section.type = "attendance";
          var obj = {
            section : this.section,
          }
          $create.call(this,obj);
        };

      }
      ,$toEditSection:function(){
        this.section.isShow = true ;
        this.section.type = "Edit";
      }
      ,$doEditSection:function(){

      }
      ,showMaterialMenu:function(sectionId, sectionIndex){
//        alert(sectionId);
        this.showMenus = true;
        this.targetSectionId = sectionId;
        this.targetSectionIndex = sectionIndex;
      },
      findTargetSection(){
        var index = this.targetSectionIndex,id = this.targetSectionId ;

        if(id == this.sections[index].sectionId){
          return this.sections[index];
        }
      },
      $doComment(val){
//          alert(val);
        if(val === "menu1"){//编辑
          var el = this.findTargetSection();
          this.beforEditSection = el ;
          el.showEditInput = true;
        }else if(val === "menu2"){//删除
          this.onShowComfirm = true;
        }
      },
      $cancelList(list){
          list.showEditInput = false;
      },
      $doEditList(list){
        var self = this,
          index = this.targetSectionIndex,
          id = this.targetSectionId ;
        if(list.name == "" ||list.length <=20 ) {
          alert("请填写课程名称");
          this.$emit("modalAlert", "请填写课程名称", "确定");
          return
        }
        if(list.link == "" || !this.reg.isUrl(list.link)) {
          alert("填写素材链接错误");
          this.$emit("modalAlert", "请填写素材链接", "确定");
          return
        }
        var obj = {
          section: {
            activityId: self.activityId,
            name: list.name,
            link: list.link,
            type: self.type,
            id  : list.sectionId
          }
        };

        list.showEditInput = false;
        edit.call(this, obj);
      },
      onCancelDel(){

      },
      onConfirmDel(){
        this.delMaterial(this.targetSectionId, this.targetSectionIndex);
        this.targetSectionId = 0;
        this.sectionIndex = 0;
      },
      delMaterial(sectionId, sectionIndex){
//        alert(sectionId)
        console.log(this.section);
        this.sections.splice(sectionIndex,1);
        var obj = {
          sectionId: sectionId
        }
        del.call(this, obj);
      },
    }
